import React from "react";
import { connect } from "react-redux";
import * as CandidateActions from "../../store/Candidate/CandidateAction";
import * as JDActions from "../../store/JobDescription/JobDescriptionAction";
import * as CActions from "../../store/Resumesource/resumesourceAction";
import { bindActionCreators } from "redux";
import { Card, CardContent } from "@material-ui/core";
import "./Timeline.css";
import timelinePersonGreen from "../../assets/images/timelinepersongreen.svg";
import timelineRightIcon from "../../assets/images/timelinerightparticon.svg";
import circleGreen from "../../assets/images/circleGreen.svg";
import circleYellow from "../../assets/images/circleYellowTimeline.svg";
import phoneIcon from "../../assets/images/phoneIconTimeline.svg";
import EmailIcon from "@material-ui/icons/Email";
import SearchInput from "../../components/Atoms/CustomizedInputs/SearchInput";
import searchIcon from "../../assets/images/Icon feather-search.svg";
import axios from "axios";
import { debounce } from "lodash";
import serverAddress from "../../config";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import ReactGA from "react-ga";

class Timeline extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      job: "",
      openAns: "",
      openRes: "",
      startDate: "",
      endDate: "",
      value: "",
      jdvalue: "",
      rp: "",
      jd_id: "",
      page: 1,
      limit: 7,
      options: [],
      timelineNames: [],
      timelineNames1: [],
      data2: [],
      jobtitle: [],
      cand_selected: {},
      candidate_details: [],
      cand_name: "",
      cand_phone: "",
      cand_email: "",
      selected: "0",
      optionsdata: [],
    };
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.props.JobDescriptionAction.getJobDescriptionData();
  }

  static getDerivedStateFromProps(nextProps, currentState) {
    if (
      nextProps.JobDescription &&
      nextProps.JobDescription.isGetJobDescriptionData
    ) {
      nextProps.JobDescription.isGetJobDescriptionData = false;

      nextProps.JobDescription.getJobDescriptionData.data.jobDescriptions.map(
        (item) => {
          currentState.options.push({ value: item.title, label: item.title });
        }
      );
    }
  }

  handleRating = (e) => {
    this.setState({ value: e.value });
  };

  handleOpenAnswers = () => {
    this.setState({ openAns: true });
  };

  handleOpenResume = () => {
    this.setState({ openRes: true });
  };

  handleClose = () => {
    this.setState({ openAns: false });
  };

  handleCloseResume = () => {
    this.setState({ openRes: false });
  };

  handleSearch = (e) => {
    this.setState({ value: e.value, searchItem: e.target.value });
  };
  debounceLog = debounce(() => {
    this.setState({ showLoader: true });
    const payload = {
      name: this.state.searchItem,
      id: this.state.jd_id,
      isDeleted: false,
    };
    this.props.resumesourceAction.searchCandidateData(payload);
  }, 700);

  handlesearchCandidate = (e) => {
    this.debounceLog();
  };

  handleSelect = (e) => {
    this.setState({ id: e.value, name: e.label });
    const payload = {
      id: e.value,
    };
    this.getdata(payload);
  };

  handleRatingOption = (e) => {
    this.setState({
      ratingOption: e.value,
    });
  };

  handleStartDate = (date) => {
    this.setState({ startDate: date });
  };

  handleEndDate = (date) => {
    this.setState({ endDate: date });
  };
  handleSelectcand = (index, timeline1, id, name, phone, email) => {
    this.setState({
      selected: index,
      cand_name: name,
      cand_phone: phone,
      cand_email: email,
    });
  };

  async getdata(payload) {
    this.props.resumesourceAction.getCandidateById(payload);
  }
  handleCand = (timeline1) => {};

  render() {
    const data = [];
    let dropDowndata = [];
    let totalPage = 1;
    let dummydata = [];
    if (
      this.props &&
      this.props.JobDescriptionReducer &&
      this.props.JobDescriptionReducer.getJobDescriptionData &&
      this.props.JobDescriptionReducer.getJobDescriptionData.data &&
      this.props.JobDescriptionReducer.getJobDescriptionData.data
        .jobDescriptions
    ) {
      dropDowndata =
        this.props.JobDescriptionReducer.getJobDescriptionData.data
          .jobDescriptions;
      this.state.optionsdata = dropDowndata.map((d) => ({
        value: d && d._id,
        label:
          " " + d &&
          d.title &&
          d.title.jobtitle + " - " + d &&
          d.minExperience + " to " + d &&
          d.maxExperience + "Years of experience",
      }));
    }
    if (
      this.props &&
      this.props.resumesourceReducer &&
      this.props.resumesourceReducer.getCandidateByIdData &&
      this.props.resumesourceReducer.getCandidateByIdData.data &&
      this.props.resumesourceReducer.getCandidateByIdData.data.resumesource
    ) {
      this.props.resumesourceReducer.getCandidateByIdData.data.resumesource.map(
        (item) => {
          let round1 = false;
          let round2 = false;
          let offer = false;
          let company = false;
          if (item.status === "ROUND 1") {
            round1 = true;
          } else if (item.status === "ROUND 2") {
            round1 = true;
            round2 = true;
          } else if (item.status === "OFFERED") {
            round1 = true;
            round2 = true;
            offer = true;
          } else if (item.status === "JOINED") {
            round1 = true;
            round2 = true;
            offer = true;
            company = true;
          }
          data.push({
            name: item.name,
            email: item.email,
            phone: item.phone,
            round1: round1,
            round2: round2,
            offer: offer,
            company: company,
          });
        }
      );
    }

    let searchData = [];

    if (
      this.props &&
      this.props.resumesourceReducer &&
      this.props.resumesourceReducer.getCandidateData &&
      this.props.resumesourceReducer.getCandidateData.data &&
      this.props.resumesourceReducer.getCandidateData.data.users
    ) {
      let data1 = [];
      this.props.resumesourceReducer.getCandidateData.data.users.map((item) => {
        data1.push({
          id: item._id,
          name: item.name,
          email: item.email,
          phone: item.phone,
        });
      });
      searchData = data1;
    }

    const timeline1 = [];

    return (
      <div>
        <Card className='timelineCard1'>
          <CardContent>
            <div className='timeline-card1-wrap'>
              <div className='time-drop'>
                <SelectInput
                  className={"react-select project"}
                  onChange={this.handleSelect}
                  options={this.state.optionsdata}
                  placeholder={
                    this.state.job ? this.state.job : "Select Job Description"
                  }
                />
              </div>
              <div className='time-searchInput'>
                <SearchInput
                  className='timeSearchInput'
                  imageOnClick={() => {}}
                  placeholder='Search For Candidate Name'
                  value={this.state.value}
                  onChange={(e) => {
                    this.handleSearch(e);
                  }}
                />
                <img
                  src={searchIcon}
                  className='time-searchicon'
                  onClick={() =>
                    this.handlesearchCandidate(this.state.searchItem)
                  }
                />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className='timelineCard2'>
          <div className='row'>
            <div className='timeline-column'>
              {this.state.searchItem
                ? searchData.map((item, index) => {
                    timeline1.push({
                      name: item.name,
                      id: index,
                    });
                    this.handleCand(timeline1);

                    return (
                      <div className='timeline-button-wrapper'>
                        <button
                          key={index}
                          onClick={() =>
                            this.handleSelectcand(
                              index,
                              timeline1,
                              item.id,
                              item.name,
                              item.phone,
                              item.email
                            )
                          }
                          className={
                            this.state.selected === index
                              ? "timeline-button selected"
                              : "timeline-button"
                          }
                        >
                          <div className='sch-avatar'>
                            <div className='sch-prof'></div>
                          </div>
                          <span className='timeline-namespan'>{item.name}</span>
                        </button>
                      </div>
                    );
                  })
                : data.map((item, index) => {
                    timeline1.push({
                      name: item.name,
                      id: index,
                    });
                    this.handleCand(timeline1);

                    return (
                      <div className='timeline-button-wrapper'>
                        <button
                          key={index}
                          onClick={() =>
                            this.handleSelectcand(
                              index,
                              timeline1,
                              item.id,
                              item.name,
                              item.phone,
                              item.email
                            )
                          }
                          className={
                            this.state.selected === index
                              ? "timeline-button selected"
                              : "timeline-button"
                          }
                        >
                          <div className='sch-avatar'>
                            <div className='sch-prof'></div>
                          </div>
                          <span className='timeline-namespan'>{item.name}</span>
                        </button>
                      </div>
                    );
                  })}
            </div>

            <div className='col-9 col-sm-6'>
              <div className='time-right'>
                <div>
                  <img src={timelineRightIcon} className='time-right-icon' />
                </div>
                <div className='timeline-username'>
                  <div className='time-username'>
                    <span>{this.state.cand_name}</span>
                  </div>
                  <div className='timeline-phones'>
                    <span>
                      <img
                        src={phoneIcon}
                        className='phone-icon'
                        alt='phoneicon'
                      />
                      {this.state.cand_phone}
                    </span>

                    <span>
                      <EmailIcon />
                      {this.state.cand_email}
                    </span>
                  </div>
                </div>
              </div>
              <div className='vl'>
                <div className='timelinedata-wrap'>
                  <img src={circleGreen} className='time-circlegreen' />
                  <div className='timeline-data'>
                    <span className='timedata-bold'>Posted</span>
                    <span>12-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleGreen} className='time-circlegreen' />
                  <div className='timeline-data'>
                    <span className='timedata-bold'>Resume Sourced</span>
                    <span>14-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleGreen} className='time-circlegreen' />
                  <div className='timeline-data'>
                    <span className='timedata-bold'>Invitation Send</span>
                    <span>17-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleGreen} className='time-circlegreen' />
                  <div className='timeline-data'>
                    <span className='timedata-bold'>Interview Session</span>
                    <span>18-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleYellow} className='time-circleyellow' />
                  <div className='timeline-data'>
                    <span className='timedata-small'>Round 1</span>
                    <span className='timedata-date-round'>18-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleYellow} className='time-circleyellow' />
                  <div className='timeline-data'>
                    <span className='timedata-small'>Round 2</span>
                    <span className='timedata-date-round'>18-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleGreen} className='time-circlegreen' />
                  <div className='timeline-data'>
                    <span className='timedata-bold'>Final Offers</span>
                    <span>30-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleGreen} className='time-circlegreen' />
                  <div className='timeline-data'>
                    <span className='timedata-bold'>Offer Accepted</span>
                    <span>31-Mar-2021</span>
                  </div>
                </div>
                <div className='timelinedata-wrap'>
                  <img src={circleGreen} className='time-circlegreen' />
                  <div className='timeline-data'>
                    <span className='timedata-bold'>Company Joined</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Card>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    CandidateReducer: state.CandidateReducer,
    JobDescriptionReducer: state.JobDescriptionReducer,
    resumesourceReducer: state.resumesourceReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    CandidateAction: bindActionCreators(CandidateActions, dispatch),
    JobDescriptionAction: bindActionCreators(JDActions, dispatch),
    resumesourceAction: bindActionCreators(CActions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Timeline);
