import React from "react";
import "./selectInput.scss";
import Select, { components } from "react-select";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";

export default function SelectInput(props) {
  const CaretDownIcon = () => {
    return (
      <ArrowDropDownIcon
        style={{ color: !props.iconWhite ? "#333" : "#fff" }}
      />
    );
  };
  const DropdownIndicator = (props) => {
    return (
      <components.DropdownIndicator {...props}>
        <CaretDownIcon />
      </components.DropdownIndicator>
    );
  };
  const customStyles = {
    control: (base) => ({
      ...base,
      height: props.height ? props.height : 34,
      minHeight: props.minHeight ? props.minHeight : 34,
      maxHeight: props.maxHeight ? props.maxHeight : 34,
      padding: props.padding ? props.padding : 0,
      borderRadius: 4,
      fontSize: props.fontSize ? props.fontSize : 11,
      color: props.color ? props.color : "#000",
      background: props.background ? props.background : "#fff",
      fontWeight: props.fontWeight ? props.fontWeight : "400",
 
    }),
    singleValue: (provided, state) => {
      const opacity = state.isDisabled ? 0.5 : 1;
      const transition = "opacity 300ms";

      return { ...provided, opacity, transition };
    },
    placeholder: (styles) => ({
      ...styles,
      color: props.color ? props.color : "#000",
      fontSize: props.fontSize ? props.fontSize : 11,
      fontWeight: props.fontWeight ? props.fontWeight : "400",
    }),
    option: (styles, { data }) => {
      const color = data.color ? data.color : "#000";
      return {
        ...styles,
        color: color,
      };
    },
  };

  return (
    <Select
      components={{ DropdownIndicator }}
      classNamePrefix="select-state"
      className={props.className}
      onChange={props.onChange}
      options={props.options}
      placeholder={props.placeholder}
      value={props.value}
      isDisabled={props.disabled}
      styles={customStyles}
      menuPortalTarget={document.body}
      menuPlacement={props.placement ? props.placement : "auto"}
    />
  );
}
