import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { InputLabel, FormControl, TextField } from "@material-ui/core";
import "./search.css";

const useStyles = makeStyles((theme) => ({
  root: {
    flexWrap: "wrap",
    fontFamily: "Poppins",
  },
  input: {
    "&::placeholder": {
      textOverflow: "ellipsis",
      color: "#363636",
    },
  },
}));

function SearchInput(props) {
  const classes = useStyles();

  let icon = null;
  if (props.inputIcon) {
    icon = (
      <img
        className={props.imageClass ? props.imageClass : "img-icon"}
        src={props.inputIcon}
        height={props.imageHeight}
        width={props.imageWidth}
        role="button"
        onClick={props.imageOnClick}
      />
    );
  }

  return (
    <div style={{ width: "100%" }}>
      {props.labelName ? (
        <InputLabel
          htmlFor="bootstrap-input"
          className={props.noMargin ? "text-black " : "text-black  "}
          style={{ fontFamily: "Poppins", color: "#162855" }}
        >
          {props.required ? `${props.labelName} *` : props.labelName}
        </InputLabel>
      ) : null}
      <FormControl
        // className={classes.margin}
        style={props.deviceAddInput ? { width: "88%" } : { width: "100%" }}
        error={props.error}
      >
        <TextField
          id="bootstrap-input"
          name={props.name ? props.name : ""}
          placeholder={props.placeholder}
          onChange={props.onChange}
          type={props.type ? props.type : "text"}
          className={`${props.className} input-fields`}
          size={props.size ? props.size : "small"}
          error={props.error}
          required={props.required}
          inputProps={{
            maxLength: props.inputProps ? props.inputProps : 100,
          }}
          helperText={props.helperText}
          InputProps={{
            className: classes.textInput,
            endAdornment: icon,
            disableUnderline: true,
          }}
          // InputProps={{
          
          // }}
          value={props.value}
          variant="standard"
          disabled={props.disabled}
          onKeyPress={
            props.onKeyPress
          }
        />
      </FormControl>
    </div>
  );
}

export default SearchInput;
